.oui-avatar {
    border-radius: 100%;
    height: 80px;
    width: 80px;
    background-position: center center;
    background-size: cover;
    box-shadow: 0 0 0 1.5px rgba(255, 255, 255, 0.75);
    background-image: url(data:image/svg+xml;base64,);

    &.color-admin--border {
        box-shadow: 0 0 0 1.5px map-fetch($color, ui, admin);
    }
}

.oui-avatar--small {
    height: 27px;
    width: 27px;
}

.oui-avatar--medium {
    height: 50px;
    width: 50px;
}
